:root{
    --main-white: #FDFEFE;
    --second-yellow: #FFDE81;
    --main--yellow: #FFD357;
    --main-blue: #0B5F96;
    --second-blue: #468BB7;
    --links-color: #81ecec;
    --main-black: #1E2025;
    --second-black: #424551;
    --main-title-font: "Nunito", sans-serif;
    --paragraph-font: "Quicksand", sans-serif;
    --main-title-size: 18px;
    --second-title: 14px;
    --paragraph-size: 12px;
    --span-size: 10px;
  }

.second-hero{
    position: relative;
    height: 30vh;
    width: 100%;
    min-width: 421px;
    overflow: hidden;
}

.second-hero--img{
    position: relative;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.second-hero--overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);;
}

.second-hero--button{
    position: absolute;
    bottom: 40px;
    width: 200px;
    height: 40px;
    border-radius: 8px;
    cursor: pointer;
    border: 0;
    background-color: var(--main--yellow);
    font-size: var(--main-title-size);
    font-family: var(--main-title-font);
    font-weight: bold;
    color: var(--main-white);
}

.main-hero--button:hover{
    border-radius: 8px;
    border: solid 1px var(--main--yellow);
    cursor: pointer;
    background-color: transparent;
    font-size: var(--main-title-size);
    font-family: var(--main-title-font);
    font-weight: bold;
    color: var(--main--yellow);
}


@media (min-width: 768px){
    .second-hero{
        position: relative;
        height: 30vh;
        width: 100%;
        overflow: hidden;
    }

    .second-hero--img{
        position: relative;
        height: 100%;
        width: 100%;
        object-fit: cover;
        object-position: center;
    }

    .second-hero--overlay{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(0, 0, 0, 0.5);;
    }


    
};